
.userName {
  color: #ff9800;
}

.characters {
  list-style-type: none;
  margin-bottom: 32px;
}

.character {
  display: flex;

  padding: 16px;
  margin-bottom: 16px;
}

.characterImage {
  width: 60px;
  height: 60px;
  min-width: 60px;
  flex-shrink: 0;
  border-radius: 50%;
  margin-right: 16px;
}

.characterContent {
  display: flex;
  flex-grow: 1;
}

.left {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.characterName {
  padding-right: 8px;
}

.userAnswer {
  color: #00bcd4;
}


.characterStatus.correct {
  color: #4caf50;
}

.characterStatus.wrong {
  color: #f44336;
}

.results {
  margin-top: 32px;
}

.points {
  display: flex;
  align-items: center;
  margin-top: 8px;
  flex-shrink: 0
}

.totalWrap {
  margin-top: 32px;
  text-align: right;
}

.totalPoints {

}

.bonusItems {
  list-style-type: none;
}

.bonusItem {
  padding: 16px;
  margin-bottom: 16px;
}